import LinearAlgebra from "./LinearAlgebra"; // ÐÐ»Ñ ÐÐÐ­
import ChartPoints from "./ChartPoints"; // ÐÐ¾Ð»ÑÑÐµÐ½Ð¸Ðµ Ð³ÑÐ°ÑÐ¸ÐºÐ¾Ð²
import { Point, Element } from "./Element"; // ÐÐ»Ñ ÑÑÐ°Ð³Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¸

class BeamCalculation {
    /** Beam calculation using the finite element method.
     *
     * @param {Array<Element>} elements array with Elements
     * @param {Number} split_coeff coeff for fragmentation
     */
    constructor(elements, split_coeff = 1.0) {
        // Ð Ð°Ð·Ð±Ð¸Ð²Ð°ÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½ÑÑ
        fragmentation(...arguments);

        // Ð Ð°ÑÑÑÐ¸ÑÑÐ²Ð°ÐµÐ¼ Ð±Ð°Ð»ÐºÑ
        const CALCULATION = calculate(...arguments);

        // Ð¡ÑÑÐ¾Ð¸Ð¼ ÑÐ¾ÑÐºÐ¸ Ð´Ð»Ñ Ð³ÑÐ°ÑÐ¸ÐºÐ¾Ð²
        let CP = new ChartPoints(...arguments, ...CALCULATION);
        this.solution = CP.getChartPoints();
    }

    /** Solution - JSON object with calculated data */
    getSolution() {
        return this.solution;
    }
}

// Ð­ÐºÑÐ¿Ð¾ÑÑ ÐºÐ»Ð°ÑÑÐ°
export default BeamCalculation;

function calculate(elements, split_coeff) {
    // ÐÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ¾Ð²
    const N = elements.length;

    // Ð¡ÑÑÐ¾Ð¸Ð¼ Ð³Ð»Ð¾Ð±Ð°Ð»ÑÐ½Ð°Ñ Ð¼Ð°ÑÑÐ¸ÑÑ Ð¶ÐµÑÑÐºÐ¾ÑÑÐ¸ Ð¸ Ð³Ð»Ð¾Ð±Ð°Ð»ÑÐ½ÑÐ¹ Ð²ÐµÐºÑÐ¾Ñ
    const MATRIX = LinearAlgebra.createGlobalMatrix(...arguments, N);
    const VECTOR = LinearAlgebra.createGlobalVector(...arguments, N);

    // ÐÐµÐºÑÐ¾Ñ Ð³ÑÐ°Ð½Ð¸ÑÐ½ÑÑ ÑÑÐ»Ð¾Ð²Ð¸Ð¹
    const DEFENITIONS = LinearAlgebra.createDefVector(...arguments, N);

    // Ð£ÑÐ¸ÑÑÐ²Ð°Ñ Ð³ÑÐ°Ð½Ð¸ÑÐ½ÑÐµ ÑÑÐ»Ð¾Ð²Ð¸Ñ Ð¸Ð¼ÐµÐµÐ¼
    const DMATRIX = LinearAlgebra.setDefMatrix(MATRIX, DEFENITIONS);
    const DVECTOR = LinearAlgebra.setDefVector(VECTOR, DEFENITIONS);

    // ÐÐ¾Ð»ÑÑÐ°ÐµÐ¼ Ð²ÑÐµÐ²Ð¾Ð·Ð¼Ð¾Ð¶Ð½ÑÐµ ÑÐµÑÐµÐ½Ð¸Ñ
    const SOLUTIONS = LinearAlgebra.solve(DMATRIX, DVECTOR);
    const REACTIONS = LinearAlgebra.multiply(SOLUTIONS, MATRIX);

    return [SOLUTIONS, REACTIONS];
}

function fragmentation(elems, split_coeff) {
    let count; // ÐÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ Ð½Ð¾Ð²ÑÑ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ¾Ð² Ð´Ð»Ñ "ÑÑÐ°ÑÐ¾Ð³Ð¾" ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°
    let new_point; // ÐÐ»Ñ Ð·Ð°Ð¿Ð¸ÑÐ¸ Ð½Ð¾Ð²Ð¾Ð¹ ÑÐ¾ÑÐºÐ¸
    let new_elem; // ÐÐ»Ñ Ð·Ð°Ð¿Ð¸ÑÐ¸ Ð½Ð¾Ð²Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°

    // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ñ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ¾Ð² Ñ ÐºÐ¾Ð½ÑÐ°
    for (let i = elems.length - 1; i >= 0; i--) {
        count = elems[i].length / split_coeff;

        for (let j = 1; j < count; j++) {
            // Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð´Ð¾Ð¿Ð¾Ð»Ð½Ð¸ÑÐµÐ»ÑÐ½ÑÑ Ð¿ÑÑÑÑÑ ÑÐ¾ÑÐºÑ
            new_point = new Point([
                elems[i].points[1].coordinates[0] - split_coeff,
                0
            ]);

            // ÐÑÐ±Ð»Ð¸ÑÑÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½Ñ, Ð¸Ð·Ð¼ÐµÐ½ÑÑ Ð²ÑÐ¾ÑÑÑ ÑÐ¾ÑÐºÑ
            new_elem = new Element(
                [elems[i].points[0], new_point],
                elems[i].material,
                elems[i].distributed_load
            );

            // Ð£Ð¼ÐµÐ½ÑÑÐ°ÐµÐ¼ "ÑÑÐ°ÑÑÐ¹" ÑÐ»ÐµÐ¼ÐµÐ½Ñ
            elems[i].points[0] = new_point;

            // ÐÐ¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð½Ð¾Ð²ÑÐ¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ Ð¿ÐµÑÐµÐ´ "ÑÑÐ°ÑÑÐ¼"
            elems.splice(i, 0, new_elem);
        }
    }
}