<template>
  <div class="card has-table">
    <header class="card-header">
      <p class="card-header-title">List of Points</p>
      <a href="#" class="card-header-icon" aria-label="more options">
        <span class="icon">
          <i class="fas fa-redo-alt" aria-hidden="true"></i>
        </span>
      </a>
    </header>
    <div class="card-content">
      <b-table
        :data="isEmpty ? [] : points"
        :bordered="isBordered"
        :striped="isStriped"
        :narrowed="isNarrowed"
        :hoverable="isHoverable"
        :loading="isLoading"
        :mobile-cards="false"
      >
        <template slot-scope="props">
          <b-table-column field="id" label="â" width="40" numeric>{{ props.row.id }}</b-table-column>

          <b-table-column field="type" label="Type">{{ props.row.title }}</b-table-column>

          <b-table-column field="x" label="Coordinate" numeric centered>{{ props.row.x }}</b-table-column>

          <!-- <b-table-column field="angle" label="Angle" numeric>{{ props.row.angle }}</b-table-column> -->

          <b-table-column field="load" label="Load" numeric centered>{{ props.row.load }}</b-table-column>

          <!-- TODO: Align delete icon in the row -->
          <b-table-column @click="deletePoint">
            <span @click="deletePoint(props.row.id)">
              <b-icon pack="fas" icon="times" class="is-small"></b-icon>
            </span>
          </b-table-column>
        </template>
        <template slot="empty">
          <section class="section">
            <div class="content has-text-grey has-text-centered">
              <p>
                <b-icon icon="emoticon-sad" size="is-large"></b-icon>
              </p>
              <p>Nothing here.</p>
            </div>
          </section>
        </template>
      </b-table>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: "TablePoint",
  data() {
    return {
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: false,
      isHoverable: true,
      isLoading: false,
      defaultSortDirection: "asc",
      sortIcon: "arrow-up"
    };
  },
  computed: {
    points() {
      // TODO: ÐÐ¾ÑÐ¸Ð½Ð¸ÑÑ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ ÐºÐ¾Ð¾ÑÐ´Ð¸Ð½Ð°Ñ Ð´Ð»Ñ distload Ð¿Ð¾ÑÐ»Ðµ Ð½Ð°Ð¶Ð°ÑÐ¸Ñ Analyse
      let points = [...this.$store.getters.getPoints];
      points = points.map(el => {
        el.title = el.type;
        if (el.type == "Defenition") {
          if (el.def) {
            let arrLen = el.def.reduce((a, b) => a + b, 0);
            switch (arrLen) {
              case 3:
                el.title = "Fixed Def";
                break;
              case 2:
                el.title = "Pin Def";
                break;
              case 1:
                el.title = "Roller Def";
                break;
            }
          }
        }
        if (el.type == "Distload") {
          el.x = [el.distload[0], el.distload[1]];
          el.load = [el.distload[2], el.distload[3]];
        }
        return el;
      });
      // console.log(test);
      // console.log(points);
      return points;
    }
  },
  methods: {
    ...mapActions(["calculate"]),
    deletePoint(id) {
      console.log(this.points.filter(point => point.id !== id));
      this.$store.commit("DELETE_POINT", id);
      this.$buefy.toast.open({
        message: "Point removed",
        type: "is-warning",
        position: "is-bottom",
        duration: 1500,
        actionText: null
      });
      // console.log(this.points.filter(point => point.id !== id));
    }
  }
};
</script>
