function counter() {
    let counter = 1;
    return function(res) {
        console.log(
            '%cCalculation number:',
            'color: royalblue; font-weight: bold; font-size: 14px;',
            counter
        );
        counter++;
        for (const key in res) {
            console.log(
                '%c ' + key + ': ',
                'color: white; font-weight: bold; background: linear-gradient(violet, royalblue);',
                res[key]
            );
        }
    };
}

/** Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð²ÑÐ²Ð¾Ð´Ð° ÑÐ»Ð¾Ð²Ð°ÑÑ */
let output = counter();

function createPoint(_id, _type, _x, parameter) {
    let dict = {
        id: _id,
        type: _type, //"Defenition", "Momentum", "Distload",
        x: _x,
    };

    switch (_type) {
        case 'Load':
            dict['load'] = parameter;
            break;
        case 'Defenition':
            dict['def'] = parameter;
            break;
        case 'Momentum':
            dict['load'] = parameter;
            break;
        case 'Distload':
            dict['distload'] = parameter;
            break;
        default:
            break;
    }
    return dict;
}

function dist_func(p1, p2) {
    let [x1, y1] = p1;
    let [x2, y2] = p2;
    return function(x) {
        let k = (y1 - y2) / (x1 - x2);
        let b = y1 - x1 * k;
        return k * x + b; // ÐÐ°ÑÐµÐ¼ Ð´ÐµÐ»Ð¸ÑÑ Ð½Ð° 2 - ÑÐ· Ð²Ð¾Ð¾Ð±ÑÐµ Ð½Ðµ Ð½ÑÐ¶Ð½Ð¾.
    };
}
