import { NdArray, concatenate, zeros, ones, dot } from "numjs";

class LinearAlgebra {
    /** Solves a system of linear equations by Gauss-Jordan Elimination.
     *
     * @param {NdArray<NdArray<Number>>} matrix
     * @param {NdArray<Number>} vector
     *
     * @return {NdArray<Number>}
     */
    static solve(matrix, vector) {
        // Check asserts
        console.assert(
            matrix.shape[0] == matrix.shape[1],
            "LinearAlgebra.solve: matrix is not square."
        );
        console.assert(
            matrix.shape[0] == vector.shape[0],
            "Solve: vector size is not equal to matrix size."
        );

        // Private variables
        let n = vector.size;
        let flag;

        // Create slau
        let slau = concatenate(matrix.clone(), vector.reshape(n, 1));

        for (let i = 0; i < n; i++) {
            // Go by diagonally
            if (slau.get(i, i) != 1) {
                // if 0
                if (slau.get(i, i) == 0) {
                    flag = true;

                    // Remove 0
                    for (let j = i + 1; j < n - 1; j++) {
                        if (slau.get(j, i) != 0) {
                            let slaui = slau.get(i);
                            slau.set(i, slay.get(j));
                            slau.set(j, slaui);
                            flag = false;
                        }
                    }
                    // Return 0
                    if (flag) {
                        return null;
                    }
                }
                // if not equal to 1
                let slauii = slau.get(i, i);
                for (let j = 0; j < n + 1; j++) {
                    slau.set(i, j, slau.get(i, j) / slauii);
                }
            }
            // Change the lines
            for (let j = 0; j < n; j++) {
                if (j == i || slau.get(j, i) == 0) {
                    continue;
                }
                let slauji = slau.get(j, i);
                for (let k = 0; k < n + 1; k++) {
                    slau.set(j, k, slau.get(j, k) - slau.get(i, k) * slauji);
                }
            }
        }

        return slau.T.slice(-1).flatten();
    }

    static multiply(vector, matrix) {
        return dot(vector, matrix);
    }

    static createGlobalMatrix(elements, step, count) {
        // ÐÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ
        let local;

        // Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¿ÑÑÑÑÑ Ð¼Ð°ÑÑÐ¸ÑÑ Ð½ÑÐ»ÐµÐ¹ Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾Ð³Ð¾ ÑÐ°Ð·Ð¼ÐµÑÐ°
        let matrix = zeros([count * 3 + 3, count * 3 + 3]);

        // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð´Ð¸Ð°Ð³Ð¾Ð½Ð°Ð»Ð¸ Ð¼Ð°ÑÑÐ¸ÑÑ
        for (let diagonal = 0; diagonal < count; diagonal++) {
            // ÐÐ°Ð±Ð¸ÑÐ°ÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÑ Ð¼Ð°ÑÑÐ¸ÑÑ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°
            local = elements[diagonal].local_matrix;

            // ÐÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÑ Ð¼Ð°ÑÑÐ¸ÑÑ Ð² Ð³Ð»Ð¾Ð±Ð°Ð»ÑÐ½ÑÑ
            for (let i = 0; i < 6; i++) {
                for (let j = 0; j < 6; j++) {
                    matrix.set(
                        i + 3 * diagonal,
                        j + 3 * diagonal,
                        matrix.get(i + 3 * diagonal, j + 3 * diagonal) +
                            local[i][j]
                    );
                }
            }
        }
        return matrix;
    }

    static createGlobalVector(elements, step, count) {
        // ÐÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ
        let local;

        // Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¿ÑÑÑÐ¾Ð¹ Ð²ÐµÐºÑÐ¾Ñ Ð½ÑÐ»ÐµÐ¹ Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾Ð³Ð¾ ÑÐ°Ð·Ð¼ÐµÑÐ°
        let vector = zeros([count * 3 + 3]);

        // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð²ÐµÐºÑÐ¾ÑÑ
        for (let index = 0; index < count; index++) {
            // ÐÐ°Ð±Ð¸ÑÐ°ÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÐ¹ Ð²ÐµÐºÑÐ¾Ñ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°
            local = elements[index].local_vector(step);

            // ÐÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÐ¹ Ð²ÐµÐºÑÐ¾Ñ Ð² Ð³Ð»Ð¾Ð±Ð°Ð»ÑÐ½ÑÐ¹
            for (let j = 0; j < 6; j++) {
                vector.set(j + 3 * index, local[j]);
            }
        }

        // ÐÐµ Ð·Ð°Ð±ÑÐ²Ð°ÐµÐ¼ Ð¿ÑÐ¾ ÑÐ°Ð¼ÑÑ Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÑÑ ÑÐ¾ÑÐºÑ
        vector.set(3 + 3 * (count - 1), local[3]);
        vector.set(4 + 3 * (count - 1), local[4]);
        vector.set(5 + 3 * (count - 1), local[5]);

        return vector;
    }

    static createDefVector(elements, step, count) {
        // ÐÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ
        let local;

        // Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¿ÑÑÑÐ¾Ð¹ Ð²ÐµÐºÑÐ¾Ñ ÐµÐ´Ð¸Ð½Ð¸Ñ Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾Ð³Ð¾ ÑÐ°Ð·Ð¼ÐµÑÐ°
        let vector = ones([count * 3 + 3]);

        // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð²ÐµÐºÑÐ¾ÑÑ
        for (let index = 0; index < count; index++) {
            // ÐÐ°Ð±Ð¸ÑÐ°ÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÐ¹ Ð²ÐµÐºÑÐ¾Ñ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°
            local = elements[index].def_vector;

            // ÐÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð»Ð¾ÐºÐ°Ð»ÑÐ½ÑÐ¹ Ð²ÐµÐºÑÐ¾Ñ Ð² Ð³Ð»Ð¾Ð±Ð°Ð»ÑÐ½ÑÐ¹
            for (let j = 0; j < 3; j++) {
                vector.set(j + 3 * index, local[j]);
            }
        }

        // ÐÐµ Ð·Ð°Ð±ÑÐ²Ð°ÐµÐ¼ Ð¿ÑÐ¾ ÑÐ°Ð¼ÑÑ Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÑÑ ÑÐ¾ÑÐºÑ
        vector.set(3 + 3 * (count - 1), local[3]);
        vector.set(4 + 3 * (count - 1), local[4]);
        vector.set(5 + 3 * (count - 1), local[5]);

        return vector;
    }

    static setDefMatrix(matrix, def) {
        // ÐÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ
        const N = def.size;
        matrix = matrix.clone();

        // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð²ÐµÐºÑÐ¾ÑÑ def
        for (let index = 0; index < N; index++) {
            // ÐÐ°ÑÐ¾Ð´Ð¸Ð¼ Ð·Ð°ÐºÑÐµÐ¿Ð»ÐµÐ½Ð¸Ðµ
            if (def.get(index)) {
                for (let i = 0; i < N; i++) {
                    matrix.set(i, index, 0);
                    matrix.set(index, i, 0);
                }
                matrix.set(index, index, 1);
            }
        }
        return matrix;
    }

    static setDefVector(vector, def) {
        // ÐÐµÑÐµÐ¼ÐµÐ½Ð½ÑÐµ
        const N = def.size;
        vector = vector.clone();

        // ÐÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð²ÐµÐºÑÐ¾ÑÑ def
        for (let index = 0; index < N; index++) {
            // ÐÐ°ÑÐ¾Ð´Ð¸Ð¼ Ð·Ð°ÐºÑÐµÐ¿Ð»ÐµÐ½Ð¸Ðµ
            if (def.get(index)) {
                vector.set(index, 0);
            }
        }
        return vector;
    }

    	/** Lagrange interpolation
	 *
	 * @param {Array<Number>} x list of labels
	 * @param {Array<Array<Number>>} points list of bad points
	 */
	static lagrange(label, points) {
		let [x, y] = points;

		function basis(t, index) {
			let P = 1;
			for (let j = 0; j < x.length; j++) {
				if (j != index) P *= (t - x[j]) / (x[index] - x[j]);
			}
			return P;
		}

		return label.map(element => {
			let sum = 0;
			for (let i = 0; i < x.length; i++) {
				sum += y[i] * basis(element, i);
			}
			return sum;
		});
	}
}

export default LinearAlgebra;
